@mixin list-style ($margin, $padding, $list-style-type) {
  margin: $margin;
  padding: $padding;
  list-style-type: $list-style-type;
}

@mixin container-desktop {
  width: 100%;
  padding: 0 7.1%;
}

@mixin container-tablet {
  width: 100%;
  padding-left: 5.8%;
  padding-right: 5.7%;
}

@mixin container-mobile {
  width: 100%;
  padding-left: 4.6%;
  padding-right: 4.4%;
}

@mixin title-desktop {
  font-size: 34px;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 44px;
}

@mixin buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 68px;
  font-family: $poppins;
  font-size: 16px;
  font-weight: 500;
  border: solid 1px #a87b62;
  cursor: pointer;
}

@mixin more-link {
  font-family: $poppins;
  line-height: 21px;
  color: $dark;
  border-bottom: 1px solid $brown;

  &:hover {
    color: $brown;
  }

  &:active {
    color: $brown;
    opacity: 0.7;
  }
}
